export const reactQuill ='<h1>react-quill[ts]</h1><p>많은 사람들이 사용하는 텍스트 에디터 라이브러리이다.</p><p><br></p><p><strong>사용</strong></p><p>테마는 크게 snow와 bubble이 있으며 snow는 기본적인 편집기 화면으로 구성되어 있고, bubble은 텍스트를 드래그하여 원하는 수정을 할 수 있게 구성되어 있다.</p><p><br></p><ol><li><span style="color: rgb(128, 128, 128);">&lt;</span><span style="color: rgb(78, 201, 176);">ReactQuill</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; </span><span style="color: rgb(156, 220, 254);">value</span>=<span style="color: rgb(86, 156, 214);">{</span><span style="color: rgb(79, 193, 255);">content</span><span style="color: rgb(86, 156, 214);">}</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; </span><span style="color: rgb(156, 220, 254);">onChange</span>=<span style="color: rgb(86, 156, 214);">{</span><span style="color: rgb(220, 220, 170);">setContent</span><span style="color: rgb(86, 156, 214);">}</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; </span><span style="color: rgb(156, 220, 254);">modules</span>=<span style="color: rgb(86, 156, 214);">{</span>{</li><li>&nbsp; &nbsp; <span style="color: rgb(156, 220, 254);">toolbar:</span> [</li><li>&nbsp; &nbsp; &nbsp; [<span style="color: rgb(206, 145, 120);">"image"</span>],</li><li>&nbsp; &nbsp; &nbsp; [<span style="color: rgb(206, 145, 120);">"bold"</span>, <span style="color: rgb(206, 145, 120);">"italic"</span>, <span style="color: rgb(206, 145, 120);">"underline"</span>, <span style="color: rgb(206, 145, 120);">"strike"</span>], <span style="color: rgb(106, 153, 85);">// toggled buttons</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [</span><span style="color: rgb(206, 145, 120);">"blockquote"</span>, <span style="color: rgb(206, 145, 120);">"code-block"</span>],</li><li><br></li><li>&nbsp; &nbsp; &nbsp; [{ <span style="color: rgb(156, 220, 254);">header:</span> <span style="color: rgb(181, 206, 168);">1</span> }, { <span style="color: rgb(156, 220, 254);">header:</span> <span style="color: rgb(181, 206, 168);">2</span> }], <span style="color: rgb(106, 153, 85);">// custom button values</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">list:</span> <span style="color: rgb(206, 145, 120);">"ordered"</span> }, { <span style="color: rgb(156, 220, 254);">list:</span> <span style="color: rgb(206, 145, 120);">"bullet"</span> }],</li><li>&nbsp; &nbsp; &nbsp; [{ <span style="color: rgb(156, 220, 254);">script:</span> <span style="color: rgb(206, 145, 120);">"sub"</span> }, { <span style="color: rgb(156, 220, 254);">script:</span> <span style="color: rgb(206, 145, 120);">"super"</span> }], <span style="color: rgb(106, 153, 85);">// superscript/subscript</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">indent:</span> <span style="color: rgb(206, 145, 120);">"-1"</span> }, { <span style="color: rgb(156, 220, 254);">indent:</span> <span style="color: rgb(206, 145, 120);">"+1"</span> }], <span style="color: rgb(106, 153, 85);">// outdent/indent</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">direction:</span> <span style="color: rgb(206, 145, 120);">"rtl"</span> }], <span style="color: rgb(106, 153, 85);">// text direction</span></li><li><br></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">size:</span> [<span style="color: rgb(206, 145, 120);">"small"</span>, <span style="color: rgb(86, 156, 214);">false</span>, <span style="color: rgb(206, 145, 120);">"large"</span>, <span style="color: rgb(206, 145, 120);">"huge"</span>] }], <span style="color: rgb(106, 153, 85);">// custom dropdown</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">header:</span> [<span style="color: rgb(181, 206, 168);">1</span>, <span style="color: rgb(181, 206, 168);">2</span>, <span style="color: rgb(181, 206, 168);">3</span>, <span style="color: rgb(181, 206, 168);">4</span>, <span style="color: rgb(181, 206, 168);">5</span>, <span style="color: rgb(181, 206, 168);">6</span>, <span style="color: rgb(86, 156, 214);">false</span>] }],</li><li><br></li><li>&nbsp; &nbsp; &nbsp; [{ <span style="color: rgb(156, 220, 254);">color:</span> [] }, { <span style="color: rgb(156, 220, 254);">background:</span> [] }], <span style="color: rgb(106, 153, 85);">// dropdown with defaults from theme</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; &nbsp; [{ </span><span style="color: rgb(156, 220, 254);">font:</span> [] }],</li><li>&nbsp; &nbsp; &nbsp; [{ <span style="color: rgb(156, 220, 254);">align:</span> [] }],</li><li><br></li><li>&nbsp; &nbsp; &nbsp; [<span style="color: rgb(206, 145, 120);">"clean"</span>], <span style="color: rgb(106, 153, 85);">// remove formatting button</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; &nbsp; ],</span></li><li><span style="color: rgb(128, 128, 128);">&nbsp; }</span><span style="color: rgb(86, 156, 214);">}</span></li><li><span style="color: rgb(128, 128, 128);">/&gt;</span></li></ol><p><br></p><p>다양한 툴이 있으며, 필요한 툴을 선택해서 편집기에 넣을 수 있다.</p><p><br></p><p><strong>추가</strong></p><p> 더 다양한 내용을 참고하고 싶으면 아래 링크를 참조</p><p>https://quilljs.com/</p>';
export const reactCopyToClipboard = '<h1>react-copy-to-clipboard[dt]</h1><p><br></p><p>클릭하면 클립보드에 복사할 수 있게 해주는 라이브러리</p><p><br></p><ol><li><span style="color: rgb(128, 128, 128);">&lt;</span><span style="color: rgb(78, 201, 176);">CopyToClipboard</span> <span style="color: rgb(156, 220, 254);">text</span>=<span style="color: rgb(86, 156, 214);">{</span><span style="color: rgb(79, 193, 255);">content</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(128, 128, 128);">&gt;</span></li><li>&nbsp;&nbsp;&nbsp;<span style="color: rgb(128, 128, 128);">&lt;</span><span style="color: rgb(86, 156, 214);">button</span> <span style="color: rgb(156, 220, 254);">onClick</span>=<span style="color: rgb(86, 156, 214);">{</span><span style="color: rgb(220, 220, 170);">onClickPrintEdit</span><span style="color: rgb(86, 156, 214);">}</span><span style="color: rgb(128, 128, 128);">&gt;</span>출력<span style="color: rgb(128, 128, 128);">&lt;/</span><span style="color: rgb(86, 156, 214);">button</span><span style="color: rgb(128, 128, 128);">&gt;</span></li><li><span style="color: rgb(128, 128, 128);">&lt;/</span><span style="color: rgb(78, 201, 176);">CopyToClipboard</span><span style="color: rgb(128, 128, 128);">&gt;</span></li></ol><p><br></p><h2><strong>매개변수</strong></h2><p>text : 클립보드에 복사할 텍스트 [string, 필수]</p><p><br></p><p>onCopy : 텍스트가 복사될 때 호출 됨[func]</p><ol><li><span style="color: rgb(220, 220, 170);">onCopy</span>(<span style="color: rgb(156, 220, 254);">text</span>, <span style="color: rgb(156, 220, 254);">result</span>)</li></ol><p>-&gt; result(bool) : 복사 성공 여부[true/false]</p><p><br></p><p>children : CoptyToClipboard는 간단한 래핑 구성 요소이며, 태그를 렌더링 하지 않으므로 클릭을 캡처하는 데 사용하는 하위 요소가 포함되어야 한다.[node,필수]</p><ol><li>&lt;<span style="color: rgb(78, 201, 176);">CopyToClipboard</span> <span style="color: rgb(79, 193, 255);">text</span>=<span style="color: rgb(206, 145, 120);">"Hello!"</span>&gt;</li><li>&nbsp; &nbsp; &lt;<span style="color: rgb(78, 201, 176);">button</span>&gt;<span style="color: rgb(156, 220, 254);">Copy</span> <span style="color: rgb(79, 193, 255);">to</span> <span style="color: rgb(79, 193, 255);">clipboard</span>&lt;/<span style="color: rgb(156, 220, 254);">button</span>&gt;</li><li>&lt;/<span style="color: rgb(156, 220, 254);">CopyToClipboard</span>&gt;</li></ol><p><br></p><p>options : 그 외 추가적인 옵션이 있지만, 아직 이해하지 못함. 나중에 알아보기</p>'