import React from "react";
import Profil from "./Profil";
import { NavLink } from "react-router-dom";
export default function CategoryBox() {
  return (
    <div
      className="lzCategoryBox"
      style={{
        minWidth: "256px",
        backgroundColor: "#EDEDED",
        height: "100vh",
        position: "fixed",
      }}
    >
      <Profil />
      <NavLink to={"/TIL"}>메인</NavLink>
      <div>css</div>
      <div>react</div>
      <div>기능</div>
      <NavLink to={"/TIL/draggable"}>draggable</NavLink>
      <div style={{ display: "flex", flexDirection: "column" }}>
        <NavLink to={"/TIL/react-quill"}>react-quill</NavLink>
        <NavLink to={"/TIL/react-copy-to-clipboard"}>clipboard</NavLink>
      </div>
      <div>공부로그</div>
    </div>
  );
}
