import React, { useEffect, useMemo, useState } from "react";
import "./Home.css";
import calendarImage from "../../image/calendar.png";
import fastcampusIcon from "../../image/fastcampus.jpg";
import papagoIcon from "../../image/papago.jpg";
import githubIcon from "../../image/github.jpg";
import legoIcon from "../../image/lego.png";
import emptyLegoIcon from "../../image/emptyLego.png";
import npmIcon from "../../image/npm.png";
import { debounce } from "underscore";
import Todo from "./TodoBox";
import TodoBoxLater from "./TodoBoxLater";

function Home() {
  const [memoData, setMemoData] = useState<string>("");
  const onChangeMemo = useMemo(
    () =>
      debounce(
        (e: any) =>
          localStorage.setItem("memoData", JSON.stringify(e.target.value)),
        500
      ),
    []
  );

  useEffect(() => {
    const localMemoData = localStorage.getItem("memoData");
    setMemoData(localMemoData ? JSON.parse(localMemoData) : "");
  }, []);

  return (
    <div className="lzBodyBox">
      <strong className="lzTitle">Today Minguk Learned</strong>
      {/* date and stopwatch */}
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          margin: "32px",
        }}
      >
        <div className="lzDate">
          <img
            className="lzDate__calendar"
            src={calendarImage}
            alt="calendar"
          />
          16일 목요일
        </div>
        <div className="lzStopwatch">00 : 00</div>
      </div>
      <div
        style={{
          display: "flex",
          flex: 1,
          marginTop: "40px",
          marginLeft: "100px",
          marginRight: "100px",
          marginBottom: "72px",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-between",
          }}
        >
          {/* IconBox */}
          <div className="lzIconBox">
            <a
              href="https://fastcampus.co.kr/"
              rel="noreferrer"
              target="_blank"
            >
              <img
                className="lzIconBox__icon"
                src={fastcampusIcon}
                alt="fastcampus"
              />
            </a>
            <a
              href="https://papago.naver.com/"
              rel="noreferrer"
              target="_blank"
            >
              <img
                className="lzIconBox__icon"
                src={papagoIcon}
                alt="fastcampus"
              />
            </a>
            <a
              href="https://github.com/jlkim909"
              rel="noreferrer"
              target="_blank"
            >
              <img
                className="lzIconBox__icon"
                src={githubIcon}
                alt="fastcampus"
              />
            </a>
            <a href="https://www.npmjs.com/" rel="noreferrer" target="_blank">
              <img className="lzIconBox__icon" src={npmIcon} alt="npm" />
            </a>
          </div>
          <div className="lzMemo">
            Memo
            <textarea
              className="lzMemo__input"
              defaultValue={memoData}
              onChange={onChangeMemo}
            />
          </div>
        </div>
        {/* center */}
        <div className="lzCenter">
          <ul className="lzCenter__ul">
            <li className="lzCenter__li--title">보충</li>
            <li className="lzCenter__li">
              <img className="lzCenter__icon" src={emptyLegoIcon} alt="lego" />
              패스트캠퍼스 : 견고한 UI 설계
            </li>
          </ul>
          <ul className="lzCenter__ul">
            <li className="lzCenter__li--title">학습</li>
            <li className="lzCenter__li">
              <img className="lzCenter__icon" src={legoIcon} alt="lego" />
              스켈레톤 UI
            </li>
          </ul>
        </div>
        <div className="lzTodoList">
          <Todo />
          <TodoBoxLater />
        </div>
      </div>
    </div>
  );
}

export default Home;
